*{margin: 0;padding: 0;font-family: 'Noto Sans KR', sans-serif;}
li{list-style: none;}
a{text-decoration: none;color: black;cursor: pointer;}



/* ********************* header ***************************** */


.header{
    display: flex;
    width: 100%;
    height: 100px;
    line-height: 100px; 
    justify-content: space-between;   
}


.header .logo a{
    font-size: 0px;
    display: block;
    width: 140px;
    background-image: url(../img/logo_navy.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 58%;
}

.header .gnb ul{
    display: flex;
}

.header .gnb ul li a{
    padding: 0 20px;
    font-size: 15px;
    font-weight: 700;
}

.header .gnb ul li:last-child a{
    border-left: 1px solid black;
}


.header .icon ul{
    display: flex;
}

.header .icon{margin-right: 20px;}
.header .icon ul li{margin: 0 20px;}


/* ********************* page_Stage ***************************** */

.page_stage{
    width: 500px;
    margin: 70px auto 20px;
    position: relative;
}

.page_stage ul{
    display: flex;
    justify-content: space-between;
    text-align: center;
}


.page_stage ul::before{
    content: "";
    position: absolute;
    z-index: -1;
    width: calc(100% - 40px); 
    height: 1px;
    top: 30%;left: 12px;
    background-color: black;
    margin-left: 10px;
}

.page_stage ul .num{
    margin: 0 auto;
    text-align: center;
    background-color: white;
    height: 40px;width: 40px;
    border-radius: 50px;
    line-height: 40px;
    font-weight: bold;
    box-shadow: 1px 10px 30px rgba(128, 128, 128, 0.593);
}


/* 로그인 페이지 단계 표시 */
.page_stage ul li:nth-child(1) .num,
.page_stage ul li:nth-child(2) .num{
    background-color: #ccc;
    border: none;
}

.page_stage ul p{
    margin-top: 10px;
}


/* ********************* wrap ***************************** */

.wrap{
    width: 600px;
    margin: 0 auto 70px;
    padding: 20px;
}

.wrap h2{
    text-align: center;
    border-bottom: 1px solid #000;
    line-height: 3;
    margin-bottom: 50px;
}

/* ********************* wrap_form ***************************** */

.wrap form{
    width: 400px;
    margin: 0 auto 65px;
}


.wrap form ul li{
    margin: 20px 0;
}


.wrap form ul li label{
    font-weight: bold;
    font-size: 15px;
}

.wrap form ul li label span{
    font-weight: normal;
    font-size: 11px;
    color: #ccc;
}

.wrap form ul li .info{
    opacity: 0;
    font-weight: normal;
    font-size: 11px;
    color: red;
}

.wrap form ul li input[type=text],
.wrap form ul li input[type=password]{
    width: 100%;
    height: 50px;
    margin-top: 10px;
}


.wrap form ul li input[type=text],
.wrap form ul li input[type=password] {
    padding-left: 15px;
    font-size: 13px;
    border: 1px solid #ccc;
    border-radius: 5px;

}

.wrap form ul li{
    position: relative;
}

.wrap form ul li i{
    position: absolute;
    right: 10px;
    top: 50px;
}






 .wrap form ul:last-child .user_tel ul{
    display: flex;
}

.wrap form ul:last-child .user_tel ul li{
    margin-right: 5%;
}

.wrap form ul:last-child .user_tel ul li:last-child{
    margin-right: 0%;
}




/* ********************* user_Gender***************************** */


.wrap form .choice_list .user_gender .gender_box{
    margin-top: 10px;
}


.wrap form .choice_list .user_gender .gender_box label{
    font-weight: normal;

} 

.wrap form .choice_list .user_gender .gender_box #women{
    margin-left: 150px;
}


/* ********************* user_interest ***************************** */

.wrap form .choice_list .user_interest .interest_box{
    margin-top: 10px;
}

.wrap form .choice_list .user_interest .interest_box label{
    font-weight: normal;

} 

.wrap form .choice_list .user_interest .interest_box input{
    margin-left: 20px;
}

.wrap form .choice_list .user_interest .interest_box input:first-child{
    margin-left: 0;
}

/* ********************* user_birth ***************************** */

.wrap form .choice_list .user_birth .birth_box{
    width: 417px;
}

.wrap form .choice_list .user_birth .birth_box .year{
