    margin-right: 2%;
}

.wrap form .choice_list .user_birth .birth_box select#month{
    width: 28%;
    height: 50px;
    margin-right: 2%;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding-left: 2%;
}

.wrap form .choice_list .user_birth .birth_box select#day{
    width: 28%;
    height: 50px;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding-left: 2%;
}


/* ********************* user_mail ***************************** */






.wrap form .choice_list .user_mail .mail_box {
