}


.wrap form .choice_list .user_tel .tel_box #num01{
    width: 30%;
    height: 50px;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding-left: 2%;
}


.wrap form .choice_list .user_tel .tel_box .num_02{    
    width: 27%;
    height: 50px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.wrap form .choice_list .user_tel .tel_box .num_03{    
    width: 27%;
    height: 50px;
    border: 1px solid #ccc;
    border-radius: 5px;
}



/* ********************* user_adress ***************************** */

.wrap form ul li.adress_01,
.wrap form ul li.adress_02{
    width: 430px;
    margin: 0;
}



.adress_01 #sample6_postcode{
    width: 70%;
}

.adress_01 input[type=button]{
    border: none ;
    height: 50px;
    padding: 0 10px;
}

.adress_02 #sample6_detailAddress{
    width: 50%;
    margin-top: 0;
}

.adress_02 #sample6_extraAddress{
    width: 39%;
}




/* ********************* wrap_btn ***************************** */


.wrap .btn li:nth-child(1) a button{
    float: left;margin-right: 2%;
    background-color: #fff;
}

.wrap .btn li:nth-child(2) a button{
    float: left;
    background-color: rgb(0,32,83);
    color: white;
}

.wrap .btn li a button{
    width: 49%;
    height: 50px;
    border: 1px solid rgb(0,32,83);
    margin-bottom: 10px;
    font-size: 15px;
    letter-spacing: 2px;
    border-radius: 30px;
    font-weight: bold;
    cursor: pointer;
}